import "./card.css";

export const createCard = ({ title, items } = {}) => {
  // Create the card container
  const card = document.createElement("div");
  card.classList = "card";

  const cardTitle = document.createElement("div");
  cardTitle.classList = "card-title";
  cardTitle.textContent = title;
  card.appendChild(cardTitle);

  const borderBtn = document.createElement("div");
  borderBtn.classList = "border-btn";
  card.appendChild(borderBtn);

  // Create the list
  const list = document.createElement("ul");
  items.forEach((item) => {
    const listItem = document.createElement("li");
    listItem.classList = "card-li";

    // Create the icon element
    const cardIcon = document.createElement("icon");
    cardIcon.classList = "card-li-icon";
    iconElement.innerHTML = item.icon;

    // Create the icon element
    const cardLabel = document.createElement("span");
    cardLabel.classList = "card-li-cardLabel";
    iconElement.innerHTML = item.label;

    // Append icon and label to the list item
    listItem.appendChild(iconElement);
    listItem.appendChild(cardLabel);

    // Append list item to the list
    list.appendChild(listItem);
  });

  return card;
};

// Usage example
const personalItems = [
  { icon: '<i class="fas fa-user"></i>', label: "Profile" },
  { icon: '<i class="fas fa-lock"></i>', label: "Password" },
  { icon: '<i class="fas fa-shield-alt"></i>', label: "Data" },
];
document.body.appendChild(
  createCard({ title: "Personal", items: personalItems })
);
